.goods {
  height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  flex-wrap: wrap-reverse;
  font-size: 20px;

  &__list {
    list-style: none;
    text-transform: uppercase;
    border-radius: 3px;
    background-color: #7e754b;
    box-shadow: inset 2px 2px 2px 0 rgba(255, 255, 255, 0.5);
    padding: 5px;
    margin: 5px;
  }
}
